import React from 'react';
import { Flex, Box, Heading, Text } from 'rebass';

import IndividualProduct from './IndividualProduct';
import Pagination from '../../components/Pagination';
import CollectionStats from '../../components/CollectionStats';

function CatalogPage(props) {
  const {
    gatsbyImageProps,
  } = props.data.store.siteMetadata.gatsbyStorefrontConfig;
  const { limit, skip, cartUrl } = props.pageContext;
  const { title, description } = props.data.collection.nodes[0];
  let products = [];

  props.data.collection.nodes.forEach((node) => {
    products = [...products, ...node.products];
  });

  return (
    <Box flexWrap="wrap" pt={['26vw', '19vw', '16vw']} fontSize={['4vw', '1.8vw', '1.2vw']} sx={{ background: 'url(//cdn.shopify.com/s/files/1/0117/9005/7534/files/bg-face.jpg)' }}>
      {/* Header */}
      <Box className="card border-0 text-center" sx={{ background: 'transparent' }}>
        <Box className="card-body">
          <Box className="header" fontSize={[30, 36, 60]}>
            {title}
          </Box>
          <Box className="card-text border-0 mx-auto" width={[null, '75%']} fontSize={[1, 2, 18]}>
            {description}
          </Box>
        </Box>
          
        {/* Products */}
        <Box className="container mx-auto">
          <Box className="card rounded-0 border-bottom-0" backgroundColor="background">
            <Box className="card-body card-deck row">
              {products.map((product, index) => {
                product['cartUrl'] = cartUrl;
                if (index + 1 > skip && index + 1 <= skip + limit) {
                  return (
                    <IndividualProduct key={product.shopifyId} product={product} />
                  );
                } else {
                  return '';
                }
              })}
            </Box>

            {/* Pagination */}
            <Box className="border-top border-bottom" width={1} key="pagination">
              <Text sx={{ position: 'relative'}}>
                <Pagination {...props.pageContext} />
              </Text>
            </Box>
          </Box>
        </Box>
      </Box>
    </Box>
  );
}

export default CatalogPage;
